
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'repoCards',
  props: {
      repoName: String,
      repoUrl: String,
      repoStarCount: Number,
      repoDescription: String,
  },
  methods:{
    openLink(): void{
      window.open(this.repoUrl, "_blank");
    }
  },
});
